:root {
  --clr-blue-50: #d9f4fe;
  --clr-blue-100: #a8dbee;
  --clr-blue-400: #005796;
  --clr-blue-500: #01416f;
  --clr-orange-100: rgba(247, 148, 29, 0.26);
  --clr-orange-400: #ffa63a;
  --clr-orange-500: #f7941d;
}

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
}

section {
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

address {
  font-style: normal;
}

*,
*::after,
*::before {
  font-family: "Red Hat Display", sans-serif;
}

.btn-primary {
  padding: 0.5rem 1rem;
  border: 2px solid var(--clr-orange-500);
  background: var(--clr-orange-500);
  color: #fff;
  border-radius: 50px;
  font-weight: 600;
  font-size: 14px;
  -webkit-transition: all 280ms ease-in;
  transition: all 280ms ease-in;
  padding-right: 0rem;
}
.btn-primary > .icon {
  -webkit-transform: translateX(-1rem);
          transform: translateX(-1rem);
  opacity: 0;
  -webkit-transition: all 280ms ease-in;
  transition: all 280ms ease-in;
}
.btn-primary:hover {
  padding-right: 1rem !important;
}
.btn-primary:hover > .icon {
  -webkit-transform: translateX(0.25rem);
          transform: translateX(0.25rem);
  opacity: 1;
}

.scroll-top-btn {
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  background: var(--swiper-theme-color);
  color: #fff;
  border-radius: 131px;
  width: 40px;
  height: 40px;
  font-size: 22px;
  text-align: center;
  line-height: 40px;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  -webkit-transition: background 280ms ease, opacity 480ms ease-in-out;
  transition: background 280ms ease, opacity 480ms ease-in-out;
}
.scroll-top-btn.show {
  opacity: 1;
  pointer-events: auto;
}
.scroll-top-btn:hover, .scroll-top-btn:focus {
  background: #0266d3;
}

.announcement {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  z-index: -10;
}
.announcement-head {
  position: relative;
  padding: 0.4rem 0 0.4rem 1rem;
  color: #fff;
  font-family: "Open Sans", sans-serif;
  font-size: 12px;
  z-index: 1;
}
.announcement-head::after {
  content: "";
  position: absolute;
  top: -2px;
  left: 0;
  width: calc(100% + 10px);
  height: calc(100% + 1px);
  background: var(--clr-blue-400);
  -webkit-transform-origin: left;
          transform-origin: left;
  -webkit-transform: perspective(10px) rotateX(3deg);
          transform: perspective(10px) rotateX(3deg);
  z-index: -1;
}
.announcement-scroll {
  height: 30px;
  font-family: "Open Sans";
  font-size: 12px;
  line-height: 30px;
  background: var(--clr-orange-100);
}
.announcement-scroll a {
  color: #059fff;
}
.announcement-scroll a:hover {
  text-decoration: underline;
}
.announcement-scroll > * + *::before {
  content: "|";
  margin: 0 1rem;
  line-height: 30px;
}

